/* src/app/features/profile/profile-page.component.css */
.profile-container {
  padding: 3rem 1rem;
}
.profile-card {
  position: relative;
  text-align: center;
  background:
    linear-gradient(
      140deg,
      rgba(26, 35, 50, 1) 0%,
      rgba(15, 23, 36, 1) 95%);
  padding: 2.5rem 2rem 3rem;
  border-radius: 24px;
  color: #fff;
  margin-top: 1rem;
  overflow: hidden;
  border: 1px solid var(--border);
  box-shadow: 0 16px 44px -12px rgba(0, 0, 0, .55);
}
.profile-card:before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    radial-gradient(
      circle at 85% 15%,
      rgba(11, 110, 255, 0.18),
      transparent 65%);
}
.avatar {
  width: 110px;
  height: 110px;
  background:
    linear-gradient(
      135deg,
      #0B6EFF,
      #00C28A);
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  font-weight: 700;
  color: #fff;
  box-shadow: 0 10px 30px -8px rgba(0, 0, 0, .45);
}
.profile-card h2 {
  margin: 1rem 0 .25rem;
  font-size: 1.4rem;
  font-weight: 600;
}
.profile-card p {
  margin: 0 0 1.25rem;
  font-size: .95rem;
  color: var(--text-secondary);
}
.enrolled-courses {
  margin-top: 2.5rem;
  text-align: left;
}
.enrolled-courses h3 {
  margin: 0 0 .75rem;
  font-size: 1.15rem;
  font-weight: 600;
}
.course {
  background:
    linear-gradient(
      160deg,
      rgba(26, 35, 50, 1) 0%,
      rgba(15, 23, 36, 1) 100%);
  padding: 1rem .9rem .85rem;
  margin-top: .6rem;
  border-radius: 12px;
  color: #fff;
  border: 1px solid var(--border);
  font-size: .9rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.course span.level {
  padding: .3rem .6rem;
  border-radius: 6px;
  font-size: .65rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: .5px;
  background:
    linear-gradient(
      90deg,
      #0B6EFF,
      #00C28A);
}
/*# sourceMappingURL=profile-page.component.css.map */
